    border-right solid 1px #e6e6e6
    border-bottom solid 1px #e6e6e6
.logo__img
    color #fd6f35

.collpase-btn
    &,
    &:hover,
    &:focus
        color #606266
.breadcrumb 
    margin-left 10px
.user-info
    margin-left auto
    
.menu 
    min-height 100vh
.menu:not(.el-menu--collapse)
    width 300px
</style>